<template>
<div class="familyName" >
		<!--  -->
		<topShare></topShare>
		<div class="nameImg">
		</div>
		<!--  -->
		<div class="content" >
				<div class="masking">
						<p></p>
				</div>
		</div>
		<!--  -->
		<div class="rightNav">
				<ul>
						<li></li>
						<li></li>
						<li></li>
						<li></li>
						<li></li>
						<li></li>
				</ul>
		</div>
</div>
</template>
<script>
/**************************************/
import topShare from '../share/topShare'
/**************************************************/

/*******************js*************************/
import rNav from '../share/js/rNav'
/*****************************************************/
export default {
		data () {
				return{
						data:''
				}
		},
		components:{
				topShare
		},
		mounted() {
				this.rightFlex()
    	},
    	methods: {
    			rightFlex(){
    					this.rightNav({
								touchTier:'content',//
								rightNav:'rightNav',//
								masking:'masking'//
						});
    				}
    	}
}
</script>

<style lang="scss">
@import './css/familyName.scss';
</style>


