<template>
<div class="lookOver" >
     <div class="seekName">
          <div class="seekIcon"></div><input type="text" placeholder="" name="seekVessel" class="seekVessel" form="voteform">
          <p class="wz"></p>
          </div>
    <div class="familyShow">
        <form id="voteform">
            <div class="seekConditions">
                <input type="text" name="nativePlace" id="nativePlace" readonly="readonly" placeholder="">
                <input type="text" name="familyName" class="familyName" placeholder="">
                <label for="submit" class="submit"></label>
                <input type="submit" id="submit">
            </div>
        </form>
        <div class="familyCentent">
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
          <p>111111111</p>
        </div>
    </div>
    <div id="targetContainer"></div>
</div>
</template>
<script>
/****************************************************************/
import BottomNav from '../share/BottomNav';//
/***********************************************************************/
/***************************Js*************************************/
import pullDwon from '../share/js/pullDown';//
import MultiPicker from 'mob-multi-picker';//
/***********************************************************************/

export default { 
        components: {
                BottomNav
        },
        mounted() {
                this.a(),
                this.b()
        },
        methods: {
                a(){
                            this.pullDwon({
                                     name:"seekName",
                                     familyShow:"familyShow"
                            });
        },
                b(){
                        new MultiPicker({
                                input: 'nativePlace',//inputid
                                container: 'targetContainer',//id
                                jsonData: this.data.dataFmily,
                                success: function (arr) {
                                        var content = '';
                                                for(let i=0;i<arr.length;i++){
                                                        if(arr[i].value != ""){
                                                                if(content === ''){
                                                                        content = content+arr[i].value;
                                                                }else{
                                                                        content = content+"-"+arr[i].value;
                                                                }
                                                                        
                                                        }
                                                }
                                                        document.getElementById("nativePlace").value = content
                                                },
                                });
                        }
                }

}
</script>
<style lang="scss">
@import './css/seek.scss';
.lookOver{
        position: relative;
        width:25%;
        float:left;
}
.topShare {
        width: 100%;
        height: 2rem;
        text-align: center;
        background: #c1ad53;
        padding: 1rem;
        > h1 {
                margin: 0;
                font-size: 1.5rem;
                color: #000;
        }
}
</style>


